.main-top{
	height: 670px;
	background-color: #E9F1F3;
	.container{
		height: 100%;
		background-size: 650px;
		background-repeat: no-repeat;
		background-position: center bottom 50px;
	}
	.zag--black{
		padding: 25px 0 25px;
		position: relative;
		z-index: 10;
		pointer-events: none;
	}
	&__link{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
	}
}
.phone-block{
	float: left;
	position: relative;
	z-index: 10;
	&__zag{
		font-weight: 600;
		font-size: 18px;
		line-height: 100%;
		color: #404041;
		margin-bottom: 25px;
		padding-left: 20px;
	}
	&__ul{
		padding-left: 20px;
		position: relative;
		&::before{
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			height: 100%;
			width: 2px;
			border-radius: 2px;
			background-color: #F74B4B;
		}
		a{
			padding: 5px 5px 5px 40px;
			font-size: 18px;
			line-height: 120%;
			letter-spacing: 0.08em;
			color: #404041;
			transition: 0.2s;
			&:hover{
				margin-left: 5px;
			}
		}
	}
}
.location-block{
	float: right;
	position: relative;
	z-index: 10;
	&__ul{
		li{
			padding: 25px 22px 25px 67px;
			border-bottom: 1px solid rgba(119, 124, 126, 0.2);
			background-position: 25px 25px;
			position: relative;
			transition: 0.2s;
			left: 0;
			background-color: #fff;
			z-index: 1;

			&:first-child{
				border-top-left-radius: 5px;
				border-top-right-radius: 5px;
			}
			&:last-child{
				border: 0;
				border-bottom-left-radius: 5px;
				border-bottom-right-radius: 5px;
			}
			a{
				display: block;
				font-size: 15px;
				line-height: 200%;
				letter-spacing: -0.01em;
				color: #404041;
				span{
					text-decoration: underline;
					margin-left: 5px;
					color: #000000;
					font-weight: 500;
					&:hover{
						text-decoration: none;
					}
				}
				&:last-child{
					font-weight: 600;
					color: #000000;
				}
			}
		}
		.hover{
			box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
			z-index: 2;
		}
		.noHover{
			background-color: #F7FAFA;
		}
	}
}
.main-top__slider{
	.slick-arrow{
		height: 100%;
		z-index: 100;
		width: 10%;
		background-position: center;
		background-repeat: no-repeat;
		background-size: 30px;
		transition: .25s;
		&:before{
			display: none;
		}
	}
	.slick-next{
		background-image: url(../images/svg/a-r-black.svg);
		right: 15px;
		z-index: 1;
		&:hover{
			transform: translate(5px, -50%) scale(1.1);
		}
	}
	.slick-prev{
		background-image: url(../images/svg/a-l-black.svg);
		left: 15px;
		z-index: 1;
		&:hover{
			transform: translate(-5px, -50%) scale(1.1);
		}
	}
	.slick-dots{
		bottom: 20px;
		li{
			button:before{
				font-size: 10px;
				transition: .25s;
			}
			&:hover{
				button:before{
					transform: scale(1.2);
				}
			}
		}
			
	}
	.slick-list.draggable{
		padding: 0 !important;
	}
}